.Reg{
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}
.RegWrapper{
    width: 70%;
    height: 70%;
    /* background-color: aqua; */
    display: flex;
}
.loginLeft, .loginRight{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.loginLeft{
    margin-right: 15px;
}
.loginLogo{
    font-size: 60px;
    font-weight: 800;
    color: #1775ee;
    margin-bottom: 20px;
}
.loginDesc{
    font-size: 24px;
}

.RegBox{
    height: 400px;
    padding: 20px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 10px;
}
.loginInput{
    height: 50px;
    border-radius: 10px;
    border: 1px solid grey;
    font-size: 18px;
    padding-left: 20px;
}
.loginInput:focus{
    outline: none;
}
.loginButton{
    height: 50px;
    border-radius: 10px;
    border: none;
    background: #1775ee;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}
.loginForgot{
    text-align: center;
    color: #1775ee;
}
.loginRegisterButton{
    height: 50px;
    width: 60%;
    align-self: center;
    border-radius: 10px;
    border: none;
    background: #42b72a;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
} 
@media screen and (max-width: 480px) {
    .RegWrapper{
        height: 100vh;
        flex-direction: column;
        justify-content: space-evenly;
    }
    .RegBox{
        margin-bottom: 50px;
        height: 350px;
    }
}